import { path_image } from "../datas/PathList";
import Skills from "./Skills";
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from "react-responsive-carousel";

function Hobbies() {
  return (
    <div className="hobbies">
      <h1 className="title_hobbies">Mes centres d'intrt :</h1>
      <Carousel
        autoPlay="true"
        interval="3000"
        transitionTime="1500"
        useKeyboardArrows="true"
        showStatus="false"
        infiniteLoop="true"
      >
        {path_image.map((image) => (
          <div>
            <img src={image.path} key={image.key} alt={image.key} />
            <p className="legend">{image.describe}</p>
          </div>
        ))}
      </Carousel>
      <Skills />
    </div>
  );
}

export default Hobbies;
