import Chart from 'react-apexcharts';

function Skills() {
  return (
    <div className="skills">
      <h1 className="titleSkills">Mes comptences :</h1>
      <Chart 
            type="donut"
            width={500}
            height={ 320}
            series={[20,20,20,20,10,5,5]}
            
            options={{
             labels:['js','html','css','reactJS','java','python', 'github'],
             colors: [ '#f7df1e',
             '#ec6c29',
             '#1e7fbc',
             '#00d1f2',
             '#d62f29',
             '#f2d24a',
             '#89929b'],
             foreColor: '#fff',
             plotOptions:{
             
             pie:{
                donut:{
                    labels:{
                        show:true
                    }
                },
                legend: {
                  show: true,
                  fontSize:'40px'
                },
             }
             },
            }}
            
            />
    </div>
  );
}    

export default Skills;
