import { options } from "../datas/translation/Options";
import "../styles/App.scss";
import en from "../datas/translation/en.json";
import fr from "../datas/translation/fr.json";



function SelectLanguage() {

  let istranslate = false;
  function onSelectLanguage() {
    let state = document.getElementById("selectLanguage").value
    if(state === 'true'){ istranslate = true;} 
    return istranslate;
  }
  console.log(istranslate)
  return (
   
    <div>
      <select onChange={onSelectLanguage} id='selectLanguage'>
        {options.map((option) => (
          <option value={option.label}>{option.option}</option>
        ))}
      </select>
      
      <p>{istranslate ? en.test : fr.test }</p>
      
    </div>
  );
}
export default SelectLanguage;
